.about-section {
  padding: 100px 20px;
  text-align: center;
  background-color: var(--background-color);
  color: var(--text-color);
}

.about-content {
  max-width: 900px;
  margin: 0 auto;
}

.about-content h2 {
  font-size: 3em;
  margin-bottom: 40px;
  color: var(--primary-color);
  position: relative;
}

.about-content h2::after {
  content: '';
  width: 60px;
  height: 4px;
  background-color: var(--accent-color);
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: -10px;
}

.about-content p {
  font-size: 1.2em;
  line-height: 1.8;
  margin-bottom: 25px;
}
