.hero-section {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 80px); /* Adjust for navbar height */
  background-color: var(--background-color);
  color: var(--text-color);
  text-align: center;
  padding: 20px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.hero-content {
  max-width: 900px;
}

.hero-intro {
  font-size: 1.5em;
  color: var(--accent-color);
  margin-bottom: 10px;
}

.hero-name {
  font-size: 4em;
  font-weight: bold;
  margin-bottom: 20px;
  color: var(--primary-color);
}

.hero-tagline {
  font-size: 2em;
  margin-bottom: 30px;
  color: var(--text-color);
}

.hero-description {
  font-size: 1.2em;
  line-height: 1.6;
  margin-bottom: 40px;
  max-width: 700px;
  margin-left: auto;
  margin-right: auto;
}

.hero-buttons {
  display: flex;
  justify-content: center;
  gap: 20px;
}

.btn {
  padding: 15px 30px;
  border-radius: 8px;
  text-decoration: none;
  font-size: 1.1em;
  font-weight: bold;
  transition: all 0.3s ease;
}

.btn-primary {
  background-color: var(--primary-color);
  color: white;
}

.btn-primary:hover {
  background-color: var(--accent-color);
}

.btn-secondary {
  background-color: var(--secondary-color);
  color: var(--text-color);
  border: 1px solid var(--text-color);
}

.btn-secondary:hover {
  background-color: var(--text-color);
  color: var(--background-color);
}

/* Responsive adjustments */
@media screen and (max-width: 768px) {
  .hero-name {
    font-size: 3em;
  }

  .hero-tagline {
    font-size: 1.5em;
  }

  .hero-buttons {
    flex-direction: column;
    gap: 15px;
  }

  .btn {
    width: 80%;
    margin: 0 auto;
  }
}
